<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="dialogFormGrade = true">添加年级</el-button>
			<!-- <el-button type="primary">批量录入学生（上传EXCEL文件）</el-button> -->
		</div>
		<el-table :data="tableDate" ref="multipleTable" class="aip-table">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="年级ID" align="center"></el-table-column>
			<el-table-column prop="title" label="年级名称" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalPage">
			</el-pagination>
		</div>
		

		<el-dialog title="添加年级" :visible.sync="dialogFormGrade">
			<el-form :model="form">
				<div v-for="(item,index) in form.grades" :key="index">
					<el-form-item label="年级选择">
						<el-select v-model="item.title" placeholder="请选择">
							<el-option v-for="selectItem in grade" :key="selectItem.value" :label="selectItem.label" :value="selectItem.value">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="班级">
						<el-transfer v-model="item.classes" :data="classList" :titles="['班级', '选中班级']" :button-texts="['取消', '确定']"></el-transfer>
					</el-form-item>
				</div>
				<el-form-item label="操作">
					<el-button type="success" @click="addGrade">添加年级</el-button>
					<el-button type="success" @click="delGrade">删除年级</el-button>
				</el-form-item>
				<div class="submit">
					<el-form-item>
						<el-button type="primary" @click="newGrade">提交</el-button>
					</el-form-item>
				</div>
			</el-form>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/modules/student.js'
	export default {
		data() {
			return {
				dialogFormGrade: false,
				tableDate: [],
				form: {
					grades: []
				},
				grade: [{
					value: '一年级',
					label: '一年级'
				}, {
					value: '二年级',
					label: '二年级'
				}, {
					value: '三年级',
					label: '三年级'
				}, {
					value: '四年级',
					label: '四年级'
				}, {
					value: '五年级',
					label: '五年级'
				}, {
					value: '六年级',
					label: '六年级'
				}, ],
				classList: [],
				disabledDel: true,
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.generateData();
			this.getGrade();
		},
		methods: {
			getGrade() {
				var self = this;
				API.grades(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			newGrade() {
				var self = this;
				self.form.grades.forEach(item => {
					for (var i = 0; i < item.classes.length; i++) {
						item.classes[i] = {
							title: item.classes[i]
						}
					}
				})
				API.grade(this.form).then(res => {
					self.dialogFormGrade = false;
					self.$message.success('提交成功')
					self.getGrade();
					self.form = {}
				})
			},
			
			// 添加年级
			addGrade() {
				var self = this;
				self.form.grades.push({})
			},
			generateData(_) {
				var self = this;
				var classData = ['一班', '二班', '三班', '四班', '五班', '六班', '七班', '八班', '九班', '十班']
				for (let i = 0; i < classData.length; i++) {
					self.classList.push({
						key: classData[i],
						label: classData[i]
					});
				}
				return self.classList
			},
			delGrade() {
				var self = this;
				self.form.grades.pop({})
			},
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getGrade();
			},
		}
	}
</script>

<style lang="scss" scoped>
	.btn {
		margin-bottom: 1vw;
	}
	.el-table {
		border: 1px solid #dfe6ec;
	}
	
	.block {
		text-align: center;
		margin-top: 1vw;
	}
	
	.submit {
		margin-top: 1vw;
		text-align: right;
	}
</style>
